@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:ital,wght@1,400;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:ital,wght@1,400;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.NavbarListItemsDesktop ul {
  margin: 0;
  padding: 0;
  font-weight: 600;
}
.NavbarListItemsDesktop li {
  cursor: pointer;
  display: inline-block;
  margin: 20px;
}
.NavbarListItemsMobile {
  background-color: #822659;
  color: #f6f6f6;
}
.NavbarListItemsMobile li {
  cursor: pointer;
  display: flex;
  margin: 20px;
}
.Active {
  color: #3fc1c9;
}

.Intro {
  align-items: center;
  background-color: #3fc1c9;

  display: flex;
  flex-direction: column;
  justify-content: center;
  height: calc(100vh + 53px);
  text-align: center;
}
.Text {
  margin: 0px 25px;
}
.Text h2 {
  font-size: 35px;
  line-height: 3.5rem;
}
.Text h2,
p {
  color: #f6f6f6;
  font-family: "Montserrat", sans-serif;
}
.Span {
  color: #822659;
  font-family: "Crimson Text", serif;
  font-size: 60px;
}
.Button {
  background-color: #3fc1c9;
  border: 5px solid #f6f6f6;
  cursor: pointer;
  width: 225px;
}
.Button:hover {
  background-color: #822659;
  transition: all 0.5s;
}
.Button:focus {
  outline: none;
}
.Button p {
  color: #f4eeff;
  font-size: 15px;
  font-weight: 600;
}

.Link {
  display: flex;
  justify-content: space-evenly;
}
.Arrow {
  width: 20px;
  height: 20px;
  -webkit-filter: invert(100%) sepia(0%) saturate(3179%) hue-rotate(171deg)
    brightness(110%) contrast(93%);
          filter: invert(100%) sepia(0%) saturate(3179%) hue-rotate(171deg)
    brightness(110%) contrast(93%);
  position: relative;
  top: 15px;
  left: 3px;
}
section:nth-of-type(1) button .Arrow {
  transition: all 0.3s;
}
section:nth-of-type(1) button:hover .Arrow {
  transform: rotate(90deg);
  transition: all 0.3s;
}

.About {
  background-color: #f6f6f6;
  display: flex;
  justify-content: center;
  padding: 50px 50px 100px;
  text-align: center;
}
.About h1 {
  font-weight: bold;
  font-size: 35px;
  letter-spacing: 0.3rem;
  margin-bottom: 0;
  text-align: center;
}
.About p {
  color: #364f6b;
  line-height: 2rem;
  padding: 10px;
  margin: 0 200px;
}
.About h1,
h2,
h3,
p {
  color: #364f6b;
}
.About h2,
h3 {
  letter-spacing: 2px;
}
.Wrap {
  display: flex;
  justify-content: space-between;
}
.Wrap div {
  margin: 10px;
  width: 50%;
}
.Wrap img {
  border: 1px solid;
  border-radius: 100px;
  -webkit-filter: invert(27%) sepia(67%) saturate(308%) hue-rotate(170deg)
    brightness(93%) contrast(93%);
          filter: invert(27%) sepia(67%) saturate(308%) hue-rotate(170deg)
    brightness(93%) contrast(93%);
  width: 50px;
  padding: 10px;
}
.Wrap p {
  margin: 0px;
}

@media (min-width: 280px) and (max-width: 760px) {
  .Wrap {
    display: block;
  }
  .Wrap div {
    margin: 25px 5px;
    width: 100%;
  }
}
@media (min-width: 280px) and (max-width: 1026px) {
  .About p {
    margin: auto;
    text-align: center;
  }
}

.Skills {
  margin: 100px;
}
.LogoLayout {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.LogoLayout img {
  width: 75px;
  height: 75px;
  margin: 20px;
}

@media (min-width: 280px) and (max-width: 1026px) {
  .Skills {
    margin: 100px auto;
  }
  .LogoLayout img {
    margin: 12px;
  }
}

.Wrapper {
  background-color: #ebebeb;
  box-shadow: 0px 10px 15px 0px #000000;
  color: #364f6b;
  display: flex;
  justify-content: center;
  padding: 50px 50px 100px;
}
.Wrapper h1 {
  font-weight: bold;
  font-size: 35px;
  letter-spacing: 0.3rem;
  text-align: center;
}
.Projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Project {
  margin: 5px;
}
.Project h3 {
  color: #f6f6f6;
}
.ProjectOverLay {
  display: hidden;
  background: transparent;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: all 600ms ease-in-out;
  position: relative;
  width: 350px;
  height: 240px;
  top: -20px;
  left: -50px;
  padding: 25px;
}
.ProjectOverLay:hover {
  visibility: visible;
  opacity: 1;
  background: #364f6b;
  transform: scale(1.1);
  transition: all 300ms ease-in-out;
}

.ProjectLinks {
  display: flex;
  justify-content: center;
}
.ProjectLinks a {
  border: 2px solid #f6f6f6;
  color: #f6f6f6;
  display: flex;

  margin: 5px;
  text-decoration: none;
  transition: 600ms ease-in-out;
  width: 200px;
}
.ProjectLinks a:hover {
  background-color: #822659;
  color: #f6f6f6;
  transition: 300ms ease-in-out;
}
.ImagesLinkLayout {
  display: flex;
  align-items: center;
  position: relative;
  left: 25px;
  padding: 10px;
}
.ImagesLinkLayout img {
  width: 25px;
  height: 25px;
  margin-right: 5px;
  -webkit-filter: invert(99%) sepia(36%) saturate(584%) hue-rotate(167deg)
    brightness(118%) contrast(95%);
          filter: invert(99%) sepia(36%) saturate(584%) hue-rotate(167deg)
    brightness(118%) contrast(95%);
}
.ImagesLinkLayout span {
  font-size: 16px;
  font-weight: 600;
  font-family: "Montserrat", sans-serif;
}

div:nth-of-type(1) a .Images {
  transition: all 0.5s;
}

div:nth-of-type(1) a:hover .Images {
  transform: rotate(360deg);
  transition: all 0.5s;
}

.TopBox {
  width: 100%;
  height: 10px;
}
.Footer {
  align-items: center;
  background-color: #364f6b;
  color: #f6f6f6;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  padding: 25px;
}
.Footer p {
  color: #f6f6f6;
  letter-spacing: 0.1rem;
  line-height: 0;
}
.Footer span {
  color: #3fc1c9;
  font-weight: 600;
}
.ProfileLinksLayout {
  display: flex;
  align-items: center;
}
.ProfileLinks {
  background-color: #3fc1c9;
  border-radius: 2px;
  cursor: pointer;
  margin: 20px 35px;
  padding: 10px;
}
.ImageLinks {
  width: 35px;
  height: 35px;
  -webkit-filter: invert(29%) sepia(14%) saturate(1555%) hue-rotate(171deg)
    brightness(89%) contrast(87%);
          filter: invert(29%) sepia(14%) saturate(1555%) hue-rotate(171deg)
    brightness(89%) contrast(87%);
}

footer:nth-of-type(1) section {
  transition: all 0.3s;
  transform: scale(1);
}
footer:nth-of-type(1) section:hover {
  transition: background 0.3s, color 0.3s, box-shadow 0.9s, transform 0.3s;
  transform: scale(0.9);
}
footer:nth-of-type(1) section:hover img {
  -webkit-filter: invert(21%) sepia(26%) saturate(4030%) hue-rotate(295deg)
    brightness(88%) contrast(91%);
          filter: invert(21%) sepia(26%) saturate(4030%) hue-rotate(295deg)
    brightness(88%) contrast(91%);
}

h1,
h2,
h3,
p,
nav {
  font-family: "Montserrat", sans-serif;
}

nav {
  font-size: 15px;
}
span {
  font-family: "Crimson Text", serif;
}

